
import './TorrentList.css';
import React, { useEffect, useState } from "react";
import { Col, Container, Row } from 'react-bootstrap';

export default function TorrentList({ movieImdbId, serieImdbId, season, episode }) {
  const [streams, setStreams] = useState([]);

  useEffect(() => {
      if (movieImdbId) {
        const apiUrl = `https://torrentio.strem.fun/sort=seeders|qualityfilter=4k/stream/movie/${movieImdbId}.json`;
        
        fetch(apiUrl)
            .then(response => response.json())
            .then(data => {
                console.log(data);
                let streams = data.streams.map(stream => {
                  // check if contains newline
                  if (stream.title.includes('\n')) {
                    return {
                      ...stream,
                      title: stream.title.split('\n')[0],
                      subtitle: stream.title.split('\n')[1],
                    };
                  }
                  return stream;
              });
              setStreams(streams);
            })
            .catch(error => console.log(error));
      } else {
        const apiUrl = `https://torrentio.strem.fun/sort=seeders|qualityfilter=4k/stream/series/${serieImdbId}:${season}:${episode}.json`
        
        fetch(apiUrl)
            .then(response => response.json())
            .then(data => {
                console.log(data);
                let streams = data.streams.map(stream => {
                    // check if contains newline
                    if (stream.title.includes('\n')) {
                      return {
                        ...stream,
                        title: stream.title.split('\n')[0],
                        subtitle: stream.title.split('\n')[1],
                      };
                    }
                    return stream;
                });
                setStreams(streams);
            })
            .catch(error => console.log(error));
      }
  }, [movieImdbId, serieImdbId, season, episode]);

  return (
    <>
      <h2>Torrents</h2>
      <Container id='torrentList'>
        {streams.map(stream => (
            <Row className='torrentInfo' key={stream.infoHash}>
                <Col xs='12'>
                  <a className="torrentLink text-white" href={"magnet:?xt=urn:btih:" + stream.infoHash} target='_blank' rel='noopener noreferrer'>
                      {stream.title}
                      {stream.subtitle && <span className='torrentSubtitle'><br />{stream.subtitle}</span>}
                  </a>
                </Col>
            </Row>
        ))}
      </Container>
    </>
  );
}
